<template>
  <div>
      <el-table :height="250" :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))" style="width: 100%">
    <el-table-column label="编号" width="180">
         <template slot-scope="scope">
        
        <span style="margin-left: 10px">{{ scope.row.id }}</span>
         </template>
    </el-table-column>
    <el-table-column  label="姓名" width="180">
        <template slot-scope="scope">
        <el-popover trigger="hover" placement="top">
          <p>姓名: {{ scope.row.name }}</p>
          <p>住址: {{ scope.row.address }}</p>
          <div slot="reference" class="name-wrapper">
            <el-tag size="medium">{{ scope.row.name }}</el-tag>
          </div>
        </el-popover>
      </template>
    </el-table-column>
    <el-table-column label="生日" prop="birt"></el-table-column>
    <el-table-column label="性别" prop="sex"></el-table-column>
    <el-table-column label="地址" prop="address"></el-table-column>
    <el-table-column  align="right" >
        <template slot="header" slot-scope="{}">
            <el-input v-model="search" size="mini" placeholder="输入关键字搜索"/>
        </template> 
        <template slot-scope="scope">
        <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
        <el-popconfirm confirm-button-text='好的' cancel-button-text='不用了' icon="el-icon-info" icon-color="red" title="确认删除该用户吗？"
        @confirm="handleDelete(scope.$index, scope.row)"><el-button  size="mini" type="danger" slot="reference">删除</el-button>
        </el-popconfirm>
        <el-button type="success" size="mini" @click="show3 = !show3">添加</el-button>
      </template>
    </el-table-column>
  </el-table>
  <el-row>
  <el-col :span="12" :offset="12"><div><el-pagination
    layout="prev, pager, next,jumper,total,sizes"
    background
    small
    prev-text="上一页"
    next-text="下一页"
    style="margin:15px,0px"
    :page-size="size"
    :current-page="pageNow"
    :page-sizes="[2,4,6,8]"
    :total="total"
    @current-change="FindPage"
    @size-change="FindSize">
  </el-pagination></div></el-col>
</el-row>
  
  <div style="margin-top: 20px; height: 200px;">
      <el-collapse-transition>
        <div v-show="show3">
          <div class="transition-box">
              <el-form  :rules="rules"  ref="userForm" :model="form" label-width="80px">
  <el-form-item label="姓名"  prop="name">
    <el-input v-model="form.name" style="width:200px" ></el-input>
  </el-form-item>
  
  <el-form-item label="生日" prop="birt">
    
    <el-col :span="11">
      <el-date-picker type="date" value-format="yyyy-MM-dd" placeholder="选择日期" v-model="form.birt"  style="width:200px"></el-date-picker>
    </el-col>
  </el-form-item>
 
  <el-form-item label="性别" >
    <el-radio-group v-model="form.sex">
      <el-radio label="男"></el-radio>
      <el-radio label="女"></el-radio>
    </el-radio-group>
  </el-form-item>
  <el-form-item label="详细地址" prop="address" style="width:300px;  text-indent: 2em;">
    <el-input type="textarea" v-model="form.address"></el-input>
  </el-form-item>
  <el-form-item style="">
    <el-button type="primary" style="margin-left: 0%;" @click="onSubmit('userForm')">添加</el-button>
    <el-button @click="saveuserinfo">重置</el-button>
  </el-form-item>
</el-form>
          </div>
          <!-- <div class="transition-box">el-collapse-transition</div> -->
          

        </div>
      </el-collapse-transition>
  </div>
  </div>
</template>

<script>
export default {
    name:'List',
     data() {
      return {
        tableData: [],
        search:'',
        show3: false,
        form: {
          name: '',
          sex:'男',
          birt:'',
          address:'',
         
        },
        rules: {
          name: [ { required: true, message: '请输入姓名！！！', trigger: 'blur' },],
          birt:[{ required: true, message: '请输入生日信息！！！', trigger: 'blur'},],
          address:[ { required: true, message: '请输入详细地址！！！', trigger: 'blur'},]
          
        },
        total:0,
        size:4,
        pageNow:1,
      }
       
    },
    methods: {
      FindSize(size){
        this.size=size
        console.log(size)
        this.findallTableData(this.page,size)
      },
      FindPage(page){//用来处理分页的相关方法
        this.page=page
        this.findallTableData(page,this.size)
        console.log(page)
      },
      saveuserinfo(){
        this.form={'sex':"男"}
      },
      handleEdit(index, row) {
        console.log(index, row);
        this.show3=true;
        this.form=row
        // this.
      },
      handleDelete(index, row) {
        this.$http.get("http://localhost:9090/user/delete?id="+row.id).then(res=>{
          console.log(res.data)
          if(res.data.result){
             this.$message({
              message: res.data.msg,
              type: 'success',          
            })
            //刷新数据
           this.findallTableData();
          }else{
             this.$message.error('删除失败'+res.data.msg);
          }
          
        }
        )
      },
      onSubmit(userForm){
        
        this.$refs[userForm].validate((valid) => {
          if (valid) {
            this.$http.post("http://localhost:9090/user/saveorupdate",this.form).then(res=>{
           console.log(res.data)
           if(res.data.result){
              this.$message({
              message: '恭喜你'+res.data.msg,
              type: 'success',          
        });
           //清空表单
           this.form={'sex':'男'};
           //隐藏表单
           this.show3=false;
           //刷新数据
           this.findallTableData();
          } else {
             this.$message.error('添加失败'+res.data.msg);
              return false;
          }
        });
      }else{
         this.$message.error('表单数据不合法');
      }
      })
      },
      findallTableData(page,size){
        page=page?page :this.pageNow;
        size=size?size :this.size
          this.$http.get("http://localhost:9090/user/pagefind?pageNow="+page+"&pageSize="+size).then(res=>{
        this.tableData=res.data.user
        this.total=res.data.total
      })
      }
    },
    created(){
      this.findallTableData();
    }
}
</script>

<style>
 .transition-box {
    margin-bottom: 10px;
    width: 100%;
    height: 400px;
    border-radius: 4px;
    text-align: left;
    padding: 40px 20px;
    box-sizing: border-box;
    margin-right: 20px;
    margin-right: 10px;

  }

</style>